@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700&display=swap');
*{
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  /* background-image: linear-gradient(to top, #dad4ec 0%, #dad4ec 1%, #f3e7e9 100%); */
}
.app{
  /* background-image: linear-gradient(to top, #dad4ec 0%, #dad4ec 1%, #f3e7e9 100%); */
  background-image: linear-gradient(to top, #bdc2e8 0%, #bdc2e8 1%, #e6dee9 100%);
  height: 100vh;
  width: 100%;
}
.section .icon img{
  height: 50px;
}
.over{
  width: 100%;
  height: 100vh;
}
.con{
  max-width: 800px;
  height: 90%;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  padding: 1rem;
}

.sec{
  width: 100%;
  padding: 1rem;
  border-radius: 0.4 rem;
  color: white;
  
  background: rgba(255, 255, 255, 0.2);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  border: 1px solid rgba(255, 255, 255, 0.3);
}
.section{
  width: 100%;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  border: 1px solid rgba(255, 255, 255, 0.3);
}
.sec,.section,.temp{
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.sec>input{
  border: 0.8px solid white;
  border-radius: 0.4rem;
  border-color: transparent;
  /* color: rgba(255, 255, 255, 0); */
  color:rgb(35, 33, 33);
  padding: 0.5rem;
  font-size: 20px;
  font-weight: 400;
}
.sec>input:focus{
  outline: none;
}
.sec>button{
  padding: 10px 50px;
  border: none;
  border-radius: 0.4rem;
  font-size: 20px;
  font-weight: 500;
  background-color: white;;
}
.sec>button:hover{
  cursor: pointer;
  background-color: #bdc2e8;
}
.icon{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.icon>h3{
  font-size: 15px;
  font-weight: 600;
  text-transform: uppercase;
}
.temp>h1{
  font-size: 60px;
}
 
@media (max-width: 600px) {
  .con {
    padding: 0.5rem;
  }
  .sec,
  .section,
  .temp {
    padding: 0.5rem;
  }
  .sec>input {
    font-size: 16px;
  }
  .sec>button {
    padding: 8px 30px;
    font-size: 16px;
  }
  .icon>h3 {
    font-size: 12px;
  }
  .temp>h1 {
    font-size: 40px;
  }
}


@media (min-width: 601px) and (max-width: 900px) {
  .con {
    padding: 0.8rem;
  }
  .sec,
  .section,
  .temp {
    padding: 0.8rem;
  }
  .sec>input {
    font-size: 18px;
  }
  .sec>button {
    padding: 10px 40px;
    font-size: 18px;
  }
  .icon>h3 {
    font-size: 14px;
  }
  .temp>h1 {
    font-size: 50px;
  }
}